
// Footer
$footer-bg: $darker-green;
$footer-color: $beige;
$footer-font-size: 14px;
$footer-text-max-width: 300px;
$footer-link-font-size: 16px;

.footer {
  padding: 25px 25px 10px 25px;
  background-color: $footer-bg;
}

.footer p {
  font-size: $footer-font-size;
  color: $footer-color;
  text-align: center;
  margin: 0;

  @include mobile {
    max-width: $footer-text-max-width;
    margin: auto;
  }
}

.footer p.cdc {
  margin-top: 20px;
  font-size: $footer-link-font-size;
}

.footer-brand {
  @include size($width: 100%, $height: 50px);
  margin: 0 auto 10px;
  background: url("data:image/png;base64,")
    no-repeat center center;
}

.footer .social-medias {
  @include flex-center();
  width: 40%;
  margin: 20px auto 0;

  a {
    display: inline-block;
    margin: 0 40px 10px;
  }

  @include tablet {
    width: 80%;
  }
}

